* 
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  margin: 0;
  padding: 0;

ul 
  list-style: none;

html 
  scroll-behavior: smooth;

a 
  text-decoration: none;
  color: black;

.container 
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;

@media (min-width: 768px) 
  .container 
    width: 750px;
  

@media (min-width: 992px) 
  .container 
    width: 970px;
  

@media (min-width: 1200px) 
  .container 
    width: 1170px;