@mixin flex($justify, $align, $direction) 
  display: flex;
  justify-content: $justify;
  align-items: $align;
  flex-direction: $direction;


@mixin overlay($color, $opacity) 
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba($color, $opacity);


@mixin grid($size, $gap) 
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax($size, 1fr));
  gap: $gap;


@mixin grid-col($cols) 
  @for $i from 0 through $cols 
    .col-#{$i} 
      width: percentage($i / $cols);
    
  


@mixin parentPadding($padding) 
  padding-top: $padding;
  padding-bottom: $padding;


@mixin input($w, $h, $p, $br, $bck-c, $c, $pc, $b) 
  width: $w;
  height: $h;
  padding: $p;
  border-radius: $br;
  outline: none;
  border: if($b, $b, none);
  background-color: $bck-c;
  color: $c;
  &::placeholder 
    color: $pc;
  

